<template>
  <div class="receipt back">
    <div class="paypal">
      <div class="paypal__header">
        <div class="paypal__logo-wrapper">
          <img
            src="@/assets/pagarme.png"
            alt="Paypal"
            class="paypal__logo"
          />
        </div>

        <div class="paypal__header-info">
          <span class="paypal__date">{{ date }}</span>
          <span>ID da Transação </span
          ><span class="paypal__ref">{{ idTransaction }}</span>
        </div>
      </div>

      <div class="paypal__subheader-wrapper">
        <div class="paypal__subheader">
          <h1 class="paypal__username">{{ name }}, Olá</h1>
          <span class="paypal__help-text"
            >você comprou ({{ cart.length }}) itens em nossa loja:</span>
        </div>
      </div>

      <div class="paypal__cart">
        <h2 class="paypal__cart-title">Carrinho:</h2>

        <ul class="paypal__cart-list">
          <div v-if="cart.length > 0">
            <li class="paypal__cart-item" v-for="(item, index) in cart" :key="index">
              <span class="paypal__index">{{index + 1}}</span>
              <span class="paypal__item-name">{{item.title}}</span>
              <span class="paypal__item-price">R$ {{item.unit_price}}</span>
            </li>
          </div>
          <div v-else>
            <li class="paypal__cart-item">
              <span class="paypal__index">1</span>
              <span class="paypal__item-name">t-Shirt Lacoste</span>
              <span class="paypal__item-price">$48.00</span>
            </li>

            <li class="paypal__cart-item">
              <span class="paypal__index">2</span>
              <span class="paypal__item-name">Snickers Nike</span>
              <span class="paypal__item-price">$125.00</span>
            </li>

            <li class="paypal__cart-item">
              <span class="paypal__index">3</span>
              <span class="paypal__item-name">All Stars</span>
              <span class="paypal__item-price">$95.00</span>
            </li>

            <li class="paypal__cart-item">
              <span class="paypal__cart-total">Total</span>
              <span class="paypal__item-price">$268.00</span>
            </li>
          </div>
        </ul>
      </div>

      <div class="paypal__footer">
        <img
          src="@/assets/barcode.png"
          alt="Paypal Barcode"
          class="paypal__barcode"
        />
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "CheckoutReceipt",
  data() {
    return {
      date: "",
      name: "",
      idTransaction: "",
      cart: [],
    };
  },
  created() {
    if (this.$route.query.hash) {
      const decrypted = atob(this.$route.query.hash);

      const object = JSON.parse(decrypted);

      this.decrypted = object;

      this.idTransaction = object.idTransaction || "0f-113";
      this.name = object.customer.name;
      this.date = new Date()
        .toLocaleString("pt-BR")
        .replace(/T/, " ")
        .replace(/\..+/, "");
      this.cart = object.items;      
    }
  },
};
</script>

<style lang='css'>
.back {
    background-color: #3991e9;
}
html,
body {
  height: 100%;
  width: 100%;
  margin: 0;
  padding: 0;  
  font-family: "Source Sans Pro", "Helvetica", sans-serif;
  letter-spacing: 1.5px;
}
#app {
  text-align: inherit !important;
}

.inspired-by {
  display: block;
  position: relative;
  margin-bottom: 15px;
  text-align: center;
  color: #fff;
  font-size: 14px;
}

.paypal {
  position: relative;
  left: 50%;
  display: inline-block;
  /*min-width: 500px;*/
  max-width: 100%;
  width: auto;
  margin: 50px auto 25px;
  padding: 20px 0;
  clear: both;

  -webkit-transform: translateX(-50%);
  -ms-transform: translateX(-50%);
  -o-transform: translateX(-50%);
  transform: translateX(-50%);
}

.paypal__header {
  display: flex;
  -ms-align-items: flex-start;
  align-items: flex-start;

  padding: 30px 30px 45px;
  background: #fff;
  border-top-right-radius: 5px;
  border-top-left-radius: 5px;
}

.paypal__logo-wrapper {
  flex: 1 0 40%;
}

.paypal__logo {
  display: block;
  width: 130px;
  height: auto;
  margin-left: 15px;
}

.paypal__header-info {
  flex: 1 0 50%;
}

.paypal__date,
.paypal__ref {
  display: block;
  font-size: 19px;
  color: #aaa;
  font-weight: 300;
}

.paypal__date {
  margin-bottom: 5px;
}

.paypal__subheader-wrapper {
  background: #fff;
  padding-bottom: 20px;
}

.paypal__subheader {
  padding: 0 45px 0 40px;
  border-left: 5px solid #029de0;
}

.paypal__username {
  margin: 0 0 10px 0;
  font-size: 22px;
  font-weight: 600;
}

.paypal__help-text {
  color: #aaa;
  font-weight: 300;
}

.paypal__cart {
  display: block;
  padding: 25px 30px 45px;
}

.paypal__cart-title {
  display: block;
  margin-top: 0;
  margin-bottom: 25px;
  text-align: center;
}

.paypal__cart-list {
  margin: 0;
  padding: 0 15px;
  list-style: none;
}

.paypal__cart-item {
  display: block;
  padding-top: 20px;
  margin-bottom: 20px;
  border-top: 2px dashed #aaa;

  font-size: 18px;
}

.paypal__cart-item:first-child {
  border-top: 0;
}

.paypal__cart-item:last-child {
  margin-bottom: 0;
  border-top: 2px solid #ffe155;
}

.paypal__index {
  padding-right: 15px;
  color: #aaa;
  font-weight: 300;
}

.paypal__item-name {
  color: #aaa;
